<template>
  <div id="team-vue">
      <transition name="bounce">
        <div v-if="selectedMember !== null" @click="unselectMember" id="materialbox-overlay" :style="'width:' + width + ';height:' + height">
          <img :src="'static/img/membres/' + selectedMember.photo" @click="unselectMember">
          <div class="description">
            {{ selectedMember.description }}
          </div>
        </div>
      </transition>
      <div class="row" id="selector-id">
        <Member v-for="(member, index) of members"
        @click.native="selectMember(index)"
        v-bind:index="index" v-bind="member" key="index"/>
      </div>
  </div>

</template>

<script>
import $ from 'jquery'
import Member from '@/components/client/Member'

export default {
  name: 'Team',
  components: {
    Member
  },
  data () {
    return {
      members: [
        {blaz: 'Gloria', role: 'Prez\'', photo: 'gloria.jpg', description: `Prsidente la nuit, zombie le jour, vous la reconnatrez  son cri reconnaissable entre mille : ALED. Malgr le fait quelle se sente oblige de tout faire partout, cest bien grce  elle que la liste tient encore debout  ce jour. On dit que la puissance quelle retire du soutien infaillible de sa liste lui donne des pouvoirs mystiques, dune puissance suprieure  9000.`},
        {blaz: 'Antoine M.', role: 'Vice Prez\'', photo: 'menut.jpg', description: `VP de la liste, prsident des Scots. Son menu du jour est compos de whiskey, cul-sec, fortnite et sieste. Mme sil prfre enchaner les tops 1 derrire sa console, son got pour les bons alcools le pousse  sortir pour faire de nouvelles rencontres (en fait cest un prtexte pour satisfaire sa curiosit oenologique).`},
        {blaz: 'Gatan', role: 'Scrtaire', photo: 'gaetan.jpg', description: `Ponceur de lextrme et grimpeur mrite. On raconte quil na pas dormi depuis 10 ans. Convaincu quil ira en 1B alors quil va dfoncer tous les partiels de lanne. Cette secrtaire ne sarrte jamais. Caf ?`},
        {blaz: 'Julien', role: 'Trez\'', photo: 'julien.jpg', description: `Membre  part entire du Gucci gang, cet elfe sous coke sait faire apparatre de largent de nulle part grce  des procds bien obscurs pour le reste des vivants. Heureusement pour nous, il distribue largent volontiers si vous avez la facture.
Au fait, tas Circle ?`},
        {blaz: 'Guilhaume', role: 'Vice Trez\'', photo: 'guilhaume.jpg', description: `Sil brille chaque jour un peu plus dans lart de troller, Guilhaume saura vous surprendre quand vous ne vous y attendiez plus. Aprs une bonne blague on sy attache finalement, et on finit presque par oublier quil est tomb dans la soupe de la prcampagne quand il tait petit.`},
        {blaz: 'Thierry', role: 'Respo Financement', photo: 'thierry.jpg', description: `Tout droit venu de Suisse, notre Thierry manie largent comme personne, et sert la liste  bien des gards. On dit quil a tellement de contacts dans le monde du business quil doit blanchir son argent  travers la Lord of the Lists.`},
        {blaz: 'Aurlien', role: 'Respo SFX', photo: 'aurelien.jpg', description: `Homme pieux et spirituel, frre Aurlien a quitt le monastre pour vangliser lImag. Il partage ses illuminations en ralisant des films bien styls. Il a contamin la Lord avec sa fameuse punchline : a part.`},
        {blaz: 'Raoul', role: 'Respo Gondor\'s Games', photo: 'raoul.jpg', description: `Illuminant le soutien anglais par sa matrise parfaite de la langue de Shakespeare. Descendant tout droit de William Wallace, Raoul alterne avec brio les chansons paillardes et religieuses. Mac Houille Gauche, un des instigateurs de la rvolution de la Scotlist, il a d prter allgeance aux Lords aprs sa dfaite. Mais il attend patiemment son heure: one, two, twee, viva la Scotlist !`},
        {blaz: 'Andol', role: 'Respo SOS', photo: 'andeol.jpg', description: `Ce pilier de bars sera toujours l pour taccompagner lors de tes soires (sauf si cest une kfet ou quelle finit aprs 20h30). Son charisme est sublim lorsquil a bu : humour fin, loquence, agilit dans ses mouvements, dlicatesse il sera un respo SOS idal (tu pourras mme le raccompagner chez lui).`},
        {blaz: 'Romain', role: 'Prez\' JE', photo: 'romain.jpg', description: `Prsident ultra-charismatique de la J.E., il aime discuter avec tout ce qui bouge, y compris les listes adverses. Malgr ce dfaut vident, son sourire et sa bonne humeur en font quelqu'un de trs apprci au sein de la liste.`},
        {blaz: 'Lucille', role: 'Graphiste', photo: 'lucille.jpg', description: `Graphiste hors pair, elfe mrite et cavalire confirme, cest elle qui donne une image  la liste. Toujours partante pour une soire, son rire claire la Lord au quotidien.`},
        {blaz: 'Cyprien', role: 'Respo Partenariats', photo: 'cyprien.jpg', description: `Discret, fier responsable du trsor de la JE, Cyprien saura vous rendre service quand vous en avez besoin, et rire  vos blagues quand tous admettent quelles ne sont quand mme pas terribles. Il saura vous faire remarquer si le moindre dtail est de travers. Attachement garanti.`},
        {blaz: 'Mathieu', role: 'Respo Paris Sportifs', photo: 'mathieu.jpg', description: `Les bookmakers le dtestent. Certains rapportent mme quil serait responsable de quelques crises financires. Forcment, il boit pour clbrer ses exploits.`},
        {blaz: 'Antoine M.', role: 'Respo Goodies', photo: 'moreau.jpg', description: `Facilement reconnaissable grce  sa pilosit imposante, ce cher Antoine vit de bire et de pt. Oscar du meilleur acteur pour son rle de nain dans le film et le teaser, on se demande encore de quelle liste il fait partie...`},
        {blaz: 'Antoine M.', role: 'Respo Technique', photo: 'murat.jpg', description: `Wesh elle est pourrie ma description ! De toute faon j'ai le dernier mot : je mets mon veto.`},
        {blaz: 'Adam', role: 'Respo SOS', photo: 'adam.jpg', description: `Lui qui ne lsine pas sur la vodka, saura tout de mme sortir de son lit sil le faut pour servir vos SOS. Seul nain ninja existant en ce monde, il peut se faire oublier plus vite qu'un CM de BPI.`},
        {blaz: 'Lela', role: 'Respo P\'tit Dej\'', photo: 'leila.jpg', description: `Respo chor, respo ptit dej, elle va probablement dormir pendant au moins 3 ans aprs les campagnes. Nhsitez donc pas  profiter de sa compagnie avant quil ne soit trop tard !`},
        {blaz: 'Baptiste', role: 'Respo Spam', photo: 'baptiste.jpg', description: `Aussi connu sous le nom de Spamtiste, son taux d'absence en CM (4 mois daffile !) n'a pour gal que son amour du pillage des snacks de la cafet. Ses SMS sont dj blacklists par 85% des membres de la liste. Il a galement beaucoup trop de cheveux.`},
        {blaz: 'Nicolas', role: 'P\'tit Dej\'', photo: 'nicolas.jpg', description: ` mi chemin entre JCVD et lAlsace, vous trouverez Nico. Ses grands carts ou ses high-kicks vous laisseront bouche be. Amateur de Kung-Fu, de mathmatiques mais aussi de 8.6, cet alsacien de qualit naura de cesse de vous surprendre. NB : En jean, le grand cart bien sr.`},
        {blaz: 'Yassine', role: 'Ple Comm\'', photo: 'yassine.jpg', description: `Malgr son absentisme chronique, ce ftard invtr connat une bonne partie de l'Imag. Il est clbre pour ses fins de soires plutt tardives. Cest galement un excellent cuisinier.`},
        {blaz: 'Rmi', role: 'Respo Soire', photo: 'remi.jpg', description: `Ce surhomme, capable de soulever la bastille entire, est avant tout un buveur hors-pair. En somme, c'est un nain (un peu grand, et sans la barbe). Il est aussi membre assidu du TPP o il sy retrouve souvent en infriorit numrique, entour de moult Atlas.`},
        {blaz: 'Matthias', role: 'Paris Sportifs', photo: 'matthias.jpg', description: `Vous avez besoin dplucher des lgumes mais vous ne savez pas comment faire ? Matthias propose une alternative au Will Waller 2006. Vous cherchez une faon efficace de cuire votre plat ? Avec des annes de scoutisme, Matthias a la solution. Il possde en effet une astuce de cuisine infaillible pour faciliter votre vie ; les cuisiniers le dtestent !!`},
        {blaz: 'Dylan', role: 'Logistique', photo: 'dylan.jpg', description: `Un problme, des solutions. Dylan ne fait pas les choses  moiti, et le plus fou, cest quil les fait toujours bien. Apportant son sourire et sa dlicatesse bien  lui, il vous sera vite indispensable !`},
        {blaz: 'Tho', role: 'Respo Logistique', photo: 'theo.jpg', description: `Tho LaStreet ne passe pas par quatre chemin et ne fait pas dans le faux-semblant. Thal Larue le taciturne quant  lui sait se montrer enjou aprs quelques bires. Son talent pour la logistique en fait un organisateur prcieux approvisionnant la terre entire presque toujours  temps.`},
        {blaz: 'Thodore', role: 'Respo Soire/Kfet/Dev', photo: 'theodore.jpg', description: `Si vous cherchez Thodore vers midi, il y a bien un endroit o vous le trouverez : La Cafet. On ne compte plus les sandwichs prpars, ni les nettoyages ou encore les parties de tarot  son actif. Cet nergumne blond se trouve aussi tre un staffeur de qualit, tant il est doux et attentionn ! Son humour, aussi subtil (certains diront merdique) soit-il, se manifeste  travers dlgants jeux de mots en tout genre, qui ne manquent pas de provoquer des ractions allergiques ponctues de Ta gueule Thodore des autres membres de la liste. Pour finir, Thodore est un grand amateur de Kfet. Il suffit de tendre loreille pour entendre ses cordes vocales rsonner entre deux gorges de bire.`},
        {blaz: 'Charlie', role: 'P\'tit Dej\'', photo: 'charlie.jpg', description: `Nous provenant tout droit de Condorcet, ce double list Clint membre de la 4me coloc saura vous surprendre ! Notre cher Charlie manie avec perfections les bases du Sudois, et balade ses doigts avec une folle aisance sur sa basse. En fait quand on y rflchit, il aurait tout pour draguer.`},
        {blaz: 'Gautier', role: 'Respo Paris Sportifs', photo: 'gautier.jpg', description: `Sportif de haut niveau, Gautier a trouv le bon filon pour russir les paris. Quel que soit le sport ou la discipline, il est toujours l pour vous conseiller, grce  sa connaissance des paris truqus (le foot et les arbitres). A vos marques. Prt. Pariez !`},
        {blaz: 'Loc', role: 'Respo Film', photo: 'loic.jpg', description: `Dou de capacits inoues en anglais, il exprime sa crativit  travers le (trs gros) objectif de son super appareil photo. Toujours l pour goter une dizaine de nouvelles bires, il peut parfois devenir une gentille fleur (ou un fucking FOUR) sil se sent dans lambiance. Ne nettoyez pas le dance floor, lolo va sen occuper.`},
        {blaz: 'Alexandre', role: 'Respo Communication', photo: 'alexandre.jpg', description: `Petit dernier des Lords, il sest fait connatre par ses talents de ski, ou plutt sa capacit inne  faire valser les gens. Possdant une verve peu commune, et un sens de la phrase, son rle de respo comm tait tout trac. Ses concours mondialement connus  Grenoble sont des oeuvres dart. Il publie plus vite que son ombre, donc soyez prts  liker !`},
        {blaz: 'Thibault', role: 'Ple Goodies', photo: 'thibault.jpg', description: `Sympathique AST, il est toujours accompagn de son vlo, quil ne sest toujours pas fait voler ! De plus, ce magnifique petit barbu a russi en moins dune heure lexploit dapprendre  la prez  faire une roulade. Acteur de renom, cet homme est un poseur hors pair. Il aime tellement la cuisine et le sport que pour allier les deux, il n'hsite pas  laisser des ingredients au QG afin de faire un maximum daller-retour inutiles.`},
        {blaz: 'Matisse', role: 'Ple Film', photo: 'matisse.jpg', description: `Compagnon floral de Loc, cet alternant est un excellent assistant ralisateur. Dot dun score Griffor impressionnant, il risque fortement de perdre  Je nai jamais (Ou de gagner, a dpend du point de vue). Avec sa team, il nhsitera pas une seconde  vous suivre sur un fort boyard (ou Mathieu Valbuena pour les intimes).`},
        {blaz: 'Anal', role: 'Respo Kfet', photo: 'anael.jpg', description: `PapINP lrudit.
Prsent  lINP depuis sa cration, cest lhomme que tout le monde coute pour lorganisation des gros vnements. On raconte quil possde un trs grand secret pour garder sa jeunesse INPienne. King of Kfet, il est toujours l pour contrer les imprvus. Sa sagesse et son dvouement sont fortement apprcis et respects. Ses Budgets Prvisionnels sont compars au fameux tableau de Mona Lisa.`},
        {blaz: 'Denis', role: 'Ple Gondor\'s Games', photo: 'denis.jpg', description: `Le Denis est un homme enjou et sympathique. Grand amateur de lhumour noir, jaune, blanc, il nhsitera pas cependant  vous faire remarquer que vos blagues ne sont pas marrantes, il napprcie que les plaisanteries de qualit. Malgr tout, ne vous lancez pas dans un dbat avec lui, vous y laisserez vos plumes et votre chinois. Aussi surnomm Mac Houille Droite, il tait lautre membre fondateur de la Scotlist. En attendant de pouvoir putscher avec Raoul, il rside dans une des chambre dite du pouilleux  la Scotloc.`}
      ],
      selectedMember: null
    }
  },
  methods: {
    selectMember (index) {
      this.selectedMember = this.members[index]
    },
    unselectMember () {
      this.selectedMember = null
    },
    handleScroll () {
      $('div.card:visible:onScreen').removeClass('scale-out')
      this.unselectMember()
    }
  },
  computed: {
    width () {
      return window.innerWidth
    },
    height () {
      return window.innerHeight
    }
  },
  created () {
    $.expr[':'].onScreen = elem => {
      const $window = $(window)
      const viewportTop = $window.scrollTop()
      const viewportHeight = $window.height()
      const viewportBottom = viewportTop + viewportHeight
      const $elem = $(elem)
      const top = $elem.offset().top
      const height = $elem.height()
      const bottom = top + height
      return (top >= viewportTop && top < viewportBottom) ||
             (bottom > viewportTop && bottom <= viewportBottom) ||
             (height > viewportHeight && top <= viewportTop && bottom >= viewportBottom)
    }
    window.addEventListener('scroll', this.handleScroll)
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll)
  },
  mounted () {
    setTimeout(_ => $('div.card:visible:onScreen').removeClass('scale-out'), 250)
  }
}
</script>

<style scoped>
#materialbox-overlay {
  opacity: 1;
  left: 0px;
  top: 0px;
  text-align: center;
}

#materialbox-overlay img {
  max-width: calc(100% - 50px);
  max-height: calc(95% - 100px - 50px);
  margin-top: 25px;
  margin-bottom: 25px;
}

.description {
  font-size: 1rem;
  color: white;
  max-width: 90%;
  text-align: center;
  margin: auto;
}

.bounce-enter-active {
  animation: bounce-in .5s;
}
.bounce-leave-active {
  animation: bounce-in .5s reverse;
}
@keyframes bounce-in {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1.5);
  }
  100% {
    transform: scale(1);
  }
}

</style>

<style>
.clickable {
  cursor: pointer;
}
</style>



