Que dire de plus ? Lundi, toute la journe, toute l'imag pourra venir manger gratuitement.<br>
Il y aura de tout, du sucr, du sal, du vegan, de la viande, bref : pour tous les gots !<br>
Venez vous rgaler  nos cts !`},
        {nom: 'KFET mercredi 18h30 @Minp', photo: require('@/assets/img/events/minp.jpg'), description: `PAF : 7<br>Bienvenue  lAuberge du Poney Fringant !<br>
Les seigneurs de la Kfet sont heureux de vous convier au repas quils vous ont concoct, un festin digne des plus grands ! Que vous soyez un nain carnassier ou un elfe aux gots varis, vous trouverez votre bonheur  notre table ! Nous vous attendons de pied ferme pour partager les doux breuvages qui couleront  flot de nos tonneaux ! Orgie de Bires garantie ! Nos nains, elfes et autres cratures mythiques vous recevront le mercredi 7 fvrier  la MINP. Nous esprons que vos foies, estomacs et cordes vocales seront au rendez-vous ! On entend dj au loin une voix rauque compter les secs de bire ... 18 ! 19 ! 20 ! `},
